.battlePage .pageContent {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
}

.battlePage .profiles .profile {
    -ms-flex-direction: column;
        flex-direction: column;
}

.battlePage .battlePageIntroHeader {
    font-size: 1rem;
}

.battlePage .sleep {
    vertical-align: middle;
    margin: 0.25rem;
}

.battlePage .choosingTaskProps .timeWithSeal {
    z-index: 1;
    margin-left: 1rem;
}

.battlePage .choosingTaskProps .timeWithSeal .time {
    z-index: 1;
}

@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeout {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

