    opacity: 0;
    -webkit-animation: fadein 0.5s 0s, fadeout 0.5s 3s;
            animation: fadein 0.5s 0s, fadeout 0.5s 3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.battlePage .battlePageIntro .profilesBig {
    opacity: 0;
    -webkit-animation: fadein 0.5s 1s, fadeout 0.5s 3s;
