.rc-notification {
    position: fixed;
    z-index: 1000;
}

.rc-notification .absoluteBackgroundMix {
    background: #111111;
    opacity: 0.9;
}

.rc-notification-notice {
    border-radius: 0.5rem;
    -webkit-box-shadow: 0 0 4px #fffdf1;
            box-shadow: 0 0 4px #fffdf1;
    display: block;
    width: auto;
    vertical-align: middle;
    position: relative;
}
.rc-notification-notice-closable {
}
.rc-notification-notice-close {
    position: absolute;
    right: 5px;
    top: 3px;
    color: #fffdf1;
    cursor: pointer;
    outline: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 1;
    filter: alpha(opacity=20);
    text-decoration: none;
}
.rc-notification-notice-close-x:after {
    content: 'Ã—';
}
.rc-notification-fade-enter {
    opacity: 0;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
            animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.rc-notification-fade-leave {
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
            animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.rc-notification-fade-enter.rc-notification-fade-enter-active {
    -webkit-animation-name: rcNotificationFadeIn;
            animation-name: rcNotificationFadeIn;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

.rc-notification-fade-leave.rc-notification-fade-leave-active {
    -webkit-animation-name: rcDialogFadeOut;
            animation-name: rcDialogFadeOut;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

.notice {
    padding: 1rem;
}

@-webkit-keyframes rcNotificationFadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes rcNotificationFadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes rcDialogFadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes rcDialogFadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}



